import { useState, useCallback } from 'react';

/**
 * Hook simple para selecci贸n m煤ltiple sin complicaciones
 * @param {Array} items - Array de elementos a seleccionar
 * @param {Function} getItemId - Funci贸n para obtener el ID de un elemento
 * @returns {Object} - Estado y funciones de selecci贸n
 */
export const useSimpleMultiSelect = (items = [], getItemId = (item) => item.id) => {
  const [selectedItems, setSelectedItems] = useState(new Set());

  // Obtener IDs de elementos seleccionados
  const selectedIds = Array.from(selectedItems);

  // Verificar si un elemento est谩 seleccionado
  const isSelected = useCallback((item) => {
    const id = getItemId(item);
    return selectedItems.has(id);
  }, [selectedItems, getItemId]);

  // Verificar si todos los elementos est谩n seleccionados
  const isAllSelected = selectedItems.size === items.length && items.length > 0;

  // Verificar si algunos elementos est谩n seleccionados
  const isPartiallySelected = selectedItems.size > 0 && selectedItems.size < items.length;

  // Toggle selecci贸n de un elemento
  const toggleItem = useCallback((item) => {
    const id = getItemId(item);
    setSelectedItems(prev => {
      const newSet = new Set(prev);
      if (newSet.has(id)) {
        newSet.delete(id);
      } else {
        newSet.add(id);
      }
      return newSet;
    });
  }, [getItemId]);

  // Seleccionar todos los elementos
  const selectAll = useCallback(() => {
    const allIds = items.map(item => getItemId(item));
    setSelectedItems(new Set(allIds));
  }, [items, getItemId]);

  // Deseleccionar todos los elementos
  const deselectAll = useCallback(() => {
    setSelectedItems(new Set());
  }, []);

  // Toggle selecci贸n de todos los elementos
  const toggleAll = useCallback(() => {
    if (isAllSelected) {
      deselectAll();
    } else {
      selectAll();
    }
  }, [isAllSelected, selectAll, deselectAll]);

  // Limpiar selecci贸n
  const clearSelection = useCallback(() => {
    setSelectedItems(new Set());
  }, []);

  // Obtener elementos seleccionados
  const getSelectedItems = useCallback(() => {
    return items.filter(item => isSelected(item));
  }, [items, isSelected]);

  return {
    // Estado
    selectedItems,
    selectedIds,
    isAllSelected,
    isPartiallySelected,
    
    // Funciones de selecci贸n
    isSelected,
    toggleItem,
    selectAll,
    deselectAll,
    toggleAll,
    clearSelection,
    getSelectedItems,
  };
};

export default useSimpleMultiSelect;
